// * fonts
// regular(normal) -> 400
// medium -> 500
// bold -> 700

@font-face {
  font-family: "Roboto";
  font-display: swap;
  font-style: normal;
  font-weight: 400;
  src: local("Roboto-Regular"),
    url("../fonts/Roboto-Regular.woff2") format("woff2"),
    local("Roboto Regular"), url("../fonts/Roboto-Regular.woff") format("woff");
}

@font-face {
  font-family: "Roboto";
  font-display: swap;
  font-style: normal;
  font-weight: 500;
  src: local("Roboto-Medium"),
    url("../fonts/Roboto-Medium.woff2") format("woff2"), local("Roboto Medium"),
    url("../fonts/Roboto-Medium.woff") format("woff");
}

@font-face {
  font-family: "Roboto";
  font-display: swap;
  font-style: normal;
  font-weight: 700;
  src: local("Roboto-Bold"), url("../fonts/Roboto-Bold.woff2") format("woff2"),
    local("Roboto Bold"), url("../fonts/Roboto-Bold.woff") format("woff");
}
