// flat-info

.flat-info {
  // * можно было так, но ie11
  // * display: grid;
  // * column-gap: 26px;
  // * row-gap: 12px;
  // * grid-template-columns: repeat(auto-fill, minmax(118px, 1fr));
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  // * @media (min-width: @s) {
  // * grid-template-columns: repeat(2, 1fr);
  // * }

  // * @media (min-width: @l) {
  // * grid-template-columns: repeat(auto-fill, minmax(118px, 1fr));
  // * }
}

.flat-info__wrap {
  width: 50%;
  max-width: 110px;
  margin-bottom: 12px;

  @media (min-width: @s) {
    &:nth-last-of-type(-n + 2) {
      margin-bottom: 0;
    }
  }

  @media (min-width: @l) {
    &:nth-last-of-type(-n + 3) {
      margin-bottom: 0;
    }
  }
}
