// * flat-spec

.flat-spec {
  position: absolute;
  top: 40px;
  left: calc(50% - 95% / 2);
  width: 95%;
  background-color: @basic-white;
  box-shadow: 0 2px 24px rgba(0, 0, 0, 0.04);
  border-radius: 8px;
  // * можно было так, но ie11
  // * transform: translateX(-50%);

  @media (min-width: @s) {
    left: calc(50% - 92.7% / 2);
    width: 92.7%;
  }

  @media (min-width: @m) {
    left: calc(50% - 93.9% / 2);
    width: 93.9%;
  }

  @media (min-width: @l) {
    left: calc(50% - 1200px / 2);
    width: 1200px;
  }
}

.flat-spec__content {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: flex-start;
  padding-top: 16px;
  padding-bottom: 20px;

  @media (min-width: @s) {
    flex-direction: row;
    padding-top: 24px;
  }

  @media (min-width: @m) {
    justify-content: space-between;
    padding-bottom: 24px;
  }

  @media (min-width: @l) {
    justify-content: flex-start;
  }
}

.flat-spec__close-btn {
  position: absolute;
  top: 16px;
  right: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  .mp-reset();
  color: @page-gray;
  border: none;
  border-radius: 4px;
  background-color: transparent;
  // * ie11 fallback
  background-image: url("../img/icon-cross.svg");
  background-repeat: no-repeat;
  background-position: 5px 5px;
  background-size: 14px 14px;
  transition-property: color, background-color;
  transition-duration: 0.2s;
  transition-timing-function: linear;

  &:hover {
    color: @page-black;
    background-color: @page-gray-hover;
  }

  &:active {
    background-color: @page-gray-active;
  }
}

.flat-spec__close-icon {
  fill: currentColor;
}

.flat-spec__img-wrap {
  align-self: center;
  min-height: 178px;
  margin-bottom: 16px;

  @media (min-width: @s) {
    align-self: flex-start;
    margin-bottom: 0;
    margin-right: 25px;
  }
}

// * description

.flat-spec__description {
  display: flex;
  flex-direction: column;
  // ie11 bug
  // align-items: flex-start;
  width: 100%;
  padding-bottom: 20px;

  @media (min-width: @s) {
    width: 68%;
    padding-bottom: 24px;
  }

  @media (min-width: @m) {
    width: 30.6%;
    padding-bottom: 0;
  }

  @media (min-width: @l) {
    width: 450px;
    margin-right: auto;
  }
}

.flat-spec__title {
  margin-bottom: 16px;
  font-size: 20px;
  line-height: 1.4;
  font-weight: 700;
}

// * info

.flat-spec__info {
  // ie11 bug
  // align-self: center;
  max-width: 439px;
  margin-bottom: 18px;

  @media (min-width: @s) {
    align-self: flex-start;
    width: 57%;
  }

  @media (min-width: @m) {
    width: 100%;
    min-height: 126px;
  }

  @media (min-width: @l) {
    width: 384px;
    min-height: auto;
  }
}

// * tags

.flat-spec__tags {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  width: 100%;
  .mp-reset();
  list-style: none;
}

.flat-spec__tag {
  position: relative;
  min-width: 184px;
  min-height: 32px;
  margin-right: 5px;
  margin-bottom: 8px;
  padding-top: 8px;
  padding-right: 16px;
  padding-bottom: 8px;
  padding-left: 36px;
  font-weight: 500;
  border-radius: 22px;
  text-align: center;
  background-color: @basic-light;

  &:last-child {
    margin-right: 0;
    margin-bottom: 0;
  }

  .flat-spec__tag-icon {
    display: none;
  }

  @media (min-width: @s) {
    margin-bottom: 0;
  }

  @media (min-width: @m) {
    margin-bottom: 8px;
  }

  @media (min-width: @l) {
    margin-right: 16px;
    margin-bottom: 0;
  }
}

.flat-spec__tag--done {
  color: @page-green;
  background-color: #f2faf0;
  // * ie11 fallback
  background-image: url("../img/icon-tick.svg");
  background-repeat: no-repeat;
  background-size: 12px 9px;
  background-position: 14px 11px;

  .flat-spec__tag-icon {
    display: block;
  }
}

.flat-spec__tag--undone {
  color: @page-red;
  background-color: #faf0f0;
  // * ie11 fallback
  background-image: url("../img/icon-cross.svg");
  background-repeat: no-repeat;
  background-size: 10px 10px;
  background-position: 14px 11px;

  .flat-spec__tag-icon {
    display: block;
  }
}

.flat-spec__tag-icon {
  position: absolute;
  left: 14px;
  top: 50%;
  fill: currentColor;
  transform: translateY(-50%);
}

// * price

.flat-spec__price {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  padding-top: 19px;
  border-top: 1px solid @basic-light;

  @media (min-width: @s) {
    padding-top: 23px;
  }

  @media (min-width: @m) {
    flex-direction: column;
    width: 38%;
    min-height: 260px;
    padding-top: 0;
    padding-right: 12px;
    padding-left: 32px;
    border-top: none;
    border-left: 1px solid @basic-light;
  }

  @media (min-width: @l) {
    width: 313px;
    min-height: auto;
  }
}

.flat-spec__price-info {
  @media (min-width: @m) {
    margin-bottom: auto;
  }

  @media (min-width: @l) {
    margin-bottom: 28px;
  }
}

.flat-spec__text {
  margin-bottom: 12px;
  font-weight: 400;

  &:last-child {
    margin-bottom: 16px;
  }

  @media (min-width: @s) {
    &:last-child {
      margin-bottom: 24px;
    }
  }

  @media (min-width: @m) {
    &:last-child {
      margin-bottom: 110px;
    }
  }

  @media (min-width: @l) {
    &:last-child {
      margin-bottom: 0;
    }
  }
}

.flat-spec__date {
  color: @page-blue;
  font-weight: 500;
  text-decoration: none;
  transition-property: opacity;
  transition-duration: 0.2s;
  transition-timing-function: linear;

  &:hover {
    text-decoration: underline;
  }

  &:active {
    opacity: 0.7;
  }
}

.flat-spec__sum-reserve {
  font-size: 20px;
  line-height: 1.4;
  font-weight: 700;
}

.flat-spec__sum-dev {
  color: @page-red;
  font-weight: 500;
}
